:root {
  --bg: #030712;
  --surface: #111827d9;
  --surface-strong: #161e31e6;
  --surface-soft: #1c25418c;
  --text: #f8fafc;
  --muted: #94a3b8;
  --muted-strong: #cbd5f5;
  --primary: #60a5fa;
  --primary-strong: #3b82f6;
  --success: #34d399;
  --warning: #fbbf24;
  --danger: #f87171;
  --danger-soft: #f871711f;
  --border: #94a3b81f;
  --border-strong: #94a3b847;
  --glow: #3b82f659;
  --shadow: 0 24px 48px #020617a6;
  --radius: 16px;
  --radius-sm: 10px;
  --radius-lg: 22px;
  --font-base: "Inter", "Segoe UI", system-ui, -apple-system, BlinkMacSystemFont, "Helvetica Neue", sans-serif;
  --font-mono: "JetBrains Mono", "SFMono-Regular", Menlo, Consolas, monospace;
  --font-size-lg: clamp(30px, 3vw, 36px);
  --font-size-md: clamp(16px, 1.2vw, 18px);
  --font-size-sm: 14px;
  --transition: .18s ease;
}

* {
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;
}

body {
  color: var(--text);
  font-family: var(--font-base);
  font-size: var(--font-size-md);
  background-color: #0000;
  background-image: radial-gradient(circle at 100% 0, #3b82f62e, #0000 55%), radial-gradient(circle at 10% 20%, #10b9812e, #0000 45%), linear-gradient(155deg, #020617 0%, #050a1b 60%, #020617 100%);
  background-position: 0 0, 0 0, 0 0;
  background-repeat: repeat, repeat, repeat;
  background-size: auto, auto, auto;
  background-attachment: fixed;
  background-origin: padding-box, padding-box, padding-box;
  background-clip: border-box, border-box, border-box;
  margin: 0;
  line-height: 1.6;
}

a {
  color: inherit;
}

.page {
  flex-direction: column;
  gap: 28px;
  max-width: 1480px;
  margin: 0 auto;
  padding: 32px clamp(16px, 3vw, 36px) 64px;
  display: flex;
}

.card {
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  backdrop-filter: blur(18px);
  padding: clamp(20px, 2vw, 28px);
}

.hero {
  background: linear-gradient(135deg, #3b82f62e, transparent 55%), linear-gradient(225deg, #2dd4bf1a, transparent 45%), var(--surface-strong);
  border: 1px solid var(--glow);
  justify-content: space-between;
  align-items: center;
  gap: 24px;
  display: flex;
}

.hero h1 {
  font-size: var(--font-size-lg);
  letter-spacing: .02em;
  margin: 0 0 8px;
  font-weight: 700;
}

.subtitle {
  color: var(--muted);
  margin: 0;
}

.hero-actions {
  gap: 12px;
  display: flex;
}

.metrics-grid {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 18px;
  display: grid;
}

.metric-card {
  border-radius: var(--radius-lg);
  transition: transform var(--transition), box-shadow var(--transition);
  border: 1px solid #0000;
  flex-direction: column;
  gap: 8px;
  padding: 20px;
  display: flex;
}

.metric-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 40px #082f4959;
}

.metric-card.clickable {
  cursor: pointer;
  user-select: none;
}

.metric-card.clickable:active {
  transform: translateY(-2px);
}

.metric-card.active {
  border-width: 2px;
  transform: translateY(-2px);
  box-shadow: 0 16px 32px #082f4973;
}

.metric-card.active.primary {
  background: linear-gradient(135deg, #60a5fa66, #0c4a6e4d);
  border-color: #60a5facc;
}

.metric-card.active.running {
  background: linear-gradient(135deg, #38bdf859, #0e749040);
  border-color: #38bdf8cc;
}

.metric-card.active.pending {
  background: linear-gradient(135deg, #fbbf2459, #92400e40);
  border-color: #fbbf24cc;
}

.metric-card.active.success {
  background: linear-gradient(135deg, #34d39961, #10b9813d);
  border-color: #22c55ecc;
}

.metric-card.active.failed {
  background: linear-gradient(135deg, #f871715c, #7f1d1d4d);
  border-color: #f87171cc;
}

.metric-card.primary {
  background: linear-gradient(135deg, #60a5fa4d, #0c4a6e33);
  border-color: #60a5fa73;
}

.metric-card.running {
  background: linear-gradient(135deg, #38bdf840, #0e749026);
  border-color: #38bdf859;
}

.metric-card.pending {
  background: linear-gradient(135deg, #fbbf2440, #92400e26);
  border-color: #fbbf2459;
}

.metric-card.success {
  background: linear-gradient(135deg, #34d39947, #10b98124);
  border-color: #22c55e59;
}

.metric-card.failed {
  background: linear-gradient(135deg, #f8717142, #7f1d1d33);
  border-color: #f8717159;
}

.metric-label {
  font-size: var(--font-size-sm);
  text-transform: uppercase;
  letter-spacing: .12em;
  color: var(--muted);
}

.metric-value {
  font-size: clamp(32px, 3vw, 40px);
  font-weight: 700;
}

.metric-caption {
  font-size: var(--font-size-sm);
}

.metric-progress {
  flex-direction: column;
  gap: 6px;
  display: flex;
}

.controls-bar {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 18px;
  display: flex;
}

.controls-bar .search {
  flex: 260px;
}

.controls-bar input[type="search"] {
  border-radius: var(--radius-sm);
  border: 1px solid var(--border-strong);
  width: 100%;
  color: var(--text);
  font-size: var(--font-size-md);
  background: #0f172aad;
  padding: 12px 16px;
}

.controls-bar input[type="search"]:focus {
  outline-offset: 2px;
  outline: 2px solid #60a5fa73;
}

.filters {
  flex-wrap: wrap;
  gap: 8px;
  display: flex;
}

.chip {
  border: 1px solid var(--border);
  color: var(--muted-strong);
  font-size: var(--font-size-sm);
  cursor: pointer;
  transition: background var(--transition), color var(--transition), border var(--transition);
  background: #0f172aa6;
  border-radius: 999px;
  padding: 8px 16px;
}

.chip:hover {
  background: #3b82f62e;
  border-color: #60a5fa80;
}

.chip.active {
  color: var(--text);
  background: #3b82f640;
  border-color: #60a5fa8c;
}

.chip-count {
  background: #0f172a80;
  border-radius: 999px;
  margin-left: 8px;
  padding: 2px 8px;
}

.controls-right {
  gap: 16px;
  display: flex;
}

.select-group {
  font-size: var(--font-size-sm);
  flex-direction: column;
  gap: 6px;
  display: flex;
}

.select-group select {
  border: 1px solid var(--border);
  color: var(--text);
  border-radius: var(--radius-sm);
  background: #0f172ab8;
  padding: 10px 12px;
}

.layout {
  grid-template-columns: 1fr;
  align-items: start;
  gap: 24px;
  display: grid;
}

.table-card, .detail-card {
  flex-direction: column;
  gap: 18px;
  display: flex;
}

.card-header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.badge {
  border: 1px solid var(--border);
  background: #94a3b81f;
  border-radius: 999px;
  padding: 4px 10px;
}

.table-wrapper {
  border-radius: var(--radius-sm);
  border: 1px solid #94a3b81f;
  overflow: auto;
}

.table {
  border-collapse: collapse;
  width: 100%;
  min-width: 720px;
}

.table thead th {
  text-align: left;
  font-size: var(--font-size-sm);
  text-transform: uppercase;
  letter-spacing: .08em;
  color: var(--muted);
  background: #0f172a8c;
  padding: 14px 18px;
}

.table tbody td {
  border-top: 1px solid #94a3b814;
  padding: 16px 18px;
}

.table tbody tr {
  transition: background var(--transition);
}

.table tbody tr:hover {
  background: #3b82f614;
}

.table tbody tr.selected {
  background: #60a5fa1f;
}

.table tbody tr:nth-child(odd) {
  background: #94a3b808;
}

.clickable {
  cursor: pointer;
}

.progress-track {
  background: #0f172acc;
  border: 1px solid #60a5fa2e;
  border-radius: 999px;
  width: 160px;
  height: 10px;
  overflow: hidden;
}

.progress-track span {
  background: linear-gradient(90deg, var(--primary), var(--primary-strong));
  height: 100%;
  transition: width .24s ease-out;
  display: block;
}

.progress-track.mini {
  width: 100%;
  height: 8px;
}

.btn {
  border-radius: var(--radius-sm);
  color: var(--text);
  font-size: var(--font-size-sm);
  letter-spacing: .02em;
  cursor: pointer;
  transition: background var(--transition), border var(--transition), transform var(--transition);
  background: #3b82f629;
  border: 1px solid #0000;
  padding: 10px 18px;
}

.btn:hover:not(:disabled) {
  background: #3b82f647;
  transform: translateY(-1px);
}

.btn:disabled {
  opacity: .4;
  cursor: not-allowed;
}

.btn.outline {
  background: none;
  border: 1px solid #94a3b859;
}

.btn.toggle {
  min-width: 132px;
}

.btn.toggle.active {
  background: #60a5fa40;
  border-color: #60a5fa8c;
}

.pagination {
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  display: flex;
}

.muted {
  color: var(--muted);
}

.small {
  font-size: var(--font-size-sm);
}

.table-card .pagination {
  margin-top: 4px;
}

.alert {
  border-radius: var(--radius);
  font-size: var(--font-size-sm);
  border: 1px solid #0000;
  padding: 14px 18px;
}

.alert.danger {
  background: var(--danger-soft);
  color: #fecaca;
  border-color: #f8717159;
}

.alert.subtle {
  background: #94a3b814;
  border-color: #94a3b82e;
}

.empty-state {
  text-align: center;
  color: var(--muted);
  padding: 24px 0;
}

.detail-card {
  min-height: 520px;
}

.detail-header {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  gap: 18px;
  display: flex;
}

.detail-title {
  flex-direction: column;
  gap: 6px;
  min-width: 240px;
  display: flex;
}

.detail-title h2, .detail-title p {
  margin: 0;
}

.detail-header-meta {
  flex-direction: column;
  align-items: flex-end;
  gap: 12px;
  display: flex;
}

.detail-summary {
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: flex-start;
  gap: 16px;
  display: flex;
}

.detail-summary-item {
  text-align: right;
  flex-direction: column;
  gap: 4px;
  min-width: 112px;
  display: flex;
}

.summary-progress {
  align-items: center;
  gap: 8px;
  display: flex;
}

.summary-progress-value {
  font-size: var(--font-size-sm);
  font-weight: 500;
}

.progress-track.micro {
  width: 110px;
  height: 6px;
}

.detail-scope {
  border-radius: var(--radius-sm);
  background: #0f172a99;
  border: 1px solid #94a3b826;
  flex-direction: column;
  gap: 14px;
  padding: 16px;
  display: flex;
}

.detail-scope-header {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
  display: flex;
}

.detail-scope-header h3 {
  margin: 0;
  font-size: 1.05rem;
}

.detail-scope-controls {
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
  display: flex;
}

.scope-filter {
  border-radius: var(--radius-sm);
  min-width: 220px;
  color: var(--text);
  font-size: var(--font-size-sm);
  background: #0f172ab8;
  border: 1px solid #94a3b859;
  padding: 8px 12px;
}

.scope-filter:focus-visible {
  outline-offset: 2px;
  outline: 2px solid #3b82f699;
}

.scope-filter::placeholder {
  color: var(--muted);
}

.scope-list {
  border-radius: var(--radius-sm);
  background: #02061799;
  border: 1px solid #94a3b82e;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 8px;
  max-height: 240px;
  padding: 12px;
  display: grid;
  overflow: auto;
}

.scope-item {
  border-radius: var(--radius-sm);
  white-space: nowrap;
  text-overflow: ellipsis;
  background: #3b82f614;
  border: 1px solid #3b82f62e;
  align-items: center;
  gap: 10px;
  padding: 6px 10px;
  display: flex;
  overflow: hidden;
}

.scope-item:hover {
  border-color: #3b82f66b;
}

.scope-index {
  font-family: var(--font-mono);
  font-size: var(--font-size-sm);
  color: var(--primary);
  background: #3b82f633;
  border-radius: 999px;
  flex-shrink: 0;
  padding: 2px 8px;
}

.scope-name {
  text-overflow: ellipsis;
  flex: 1;
  overflow: hidden;
}

.scope-name mark {
  color: inherit;
  background: #3b82f659;
  border-radius: 4px;
  padding: 0 4px;
}

.scope-empty {
  text-align: center;
  grid-column: 1 / -1;
  padding: 28px 0;
}

.logs-toolbar {
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  display: flex;
}

.toolbar-buttons {
  gap: 12px;
  display: flex;
}

.logs-container {
  border-radius: var(--radius);
  height: clamp(280px, 38vh, 420px);
  font-family: var(--font-mono);
  background: #020617b8;
  border: 1px solid #0f172ad9;
  padding: 16px;
  font-size: 15px;
  line-height: 1.6;
  overflow: auto;
  box-shadow: inset 0 0 0 1px #60a5fa14;
}

.log-line {
  grid-template-columns: 180px 80px 1fr;
  align-items: baseline;
  gap: 16px;
  margin: 0;
  padding: 6px 0;
  display: grid;
}

.log-timestamp {
  color: var(--muted);
}

.log-level {
  letter-spacing: .02em;
  font-weight: 600;
}

.log-message {
  color: var(--muted-strong);
  white-space: pre-wrap;
}

.log-line.level-error, .log-line.level-fatal {
  color: #fecaca;
}

.log-line.level-warning {
  color: #fef3c7;
}

.empty-detail {
  text-align: center;
  color: var(--muted);
  padding: 60px 20px;
}

.status {
  font-size: var(--font-size-sm);
  text-transform: capitalize;
  border: 1px solid #0000;
  border-radius: 999px;
  padding: 6px 12px;
  font-weight: 600;
}

.status.running {
  color: #bae6fd;
  background: #38bdf82e;
  border-color: #38bdf859;
}

.status.success {
  color: #bbf7d0;
  background: #34d3992e;
  border-color: #10b98159;
}

.status.failed {
  color: #fecaca;
  background: #f871712e;
  border-color: #f8717159;
}

.status.pending {
  color: #fde68a;
  background: #fbbf242e;
  border-color: #fbbf2459;
}

.table-card .status {
  white-space: nowrap;
}

.table-card td:nth-child(3) {
  min-width: 140px;
}

.pagination .btn {
  flex-shrink: 0;
}

.logs-container::-webkit-scrollbar {
  width: 10px;
}

.logs-container::-webkit-scrollbar-track {
  background: #0f172a66;
}

.logs-container::-webkit-scrollbar-thumb {
  background: #60a5fa73;
  border-radius: 999px;
}

@media (width <= 960px) {
  .controls-bar {
    flex-direction: column;
    align-items: stretch;
  }

  .controls-right {
    justify-content: space-between;
    width: 100%;
  }

  .logs-container {
    font-size: 14px;
  }

  .log-line {
    grid-template-columns: 150px 64px 1fr;
  }
}

@media (width <= 640px) {
  .hero {
    flex-direction: column;
    align-items: flex-start;
  }

  .hero-actions {
    justify-content: stretch;
    width: 100%;
  }

  .hero-actions .btn {
    width: 100%;
  }

  .metrics-grid {
    grid-template-columns: 1fr;
  }

  .controls-right {
    flex-direction: column;
    gap: 12px;
  }

  .controls-right .select-group {
    width: 100%;
  }

  .layout {
    gap: 18px;
  }
}
